<template>
<div class="movieView" ref='movieView' v-show="win_view">
  <div class="back"></div>
  <div class="center_box">
    <youtube :video-id="videoId" :player-vars="playerVars" ref="youtube" @playing="playing"></youtube>
  </div>

</div>
</template>

<script>
import EventBus from '../lib/EventBus.js';
export default {
  name: "MovieView",
  data() {
    return {
      win_view: false,
      videoId: '',
      playerVars: {
        autoplay: 1
      }
    }
  },
  mounted() {
    this.$EventBus.$on(EventBus.MOVIE_VIEW, (data) => {
      this.win_view = data.view;
      this.videoId = data.videoId;
      if (data.view) {
        this.playVideo();
      } else {
      }
    });
    // this.player.playVideo();
  },
  methods: {
    imageDir(name, root_name = 'campaign_images') {
      if (name != '') {
        return "https://s3.ap-northeast-2.amazonaws.com/minuapp.com.rsp/" + root_name + '/' + name;
      } else {
        return '';
      }
    },
    imageLocalDir(name, root_name = 'Campagin') {
      if (name != '') {
        return "/static/" + root_name + '/' + name;
      } else {
        return '';
      }
    },
    async playVideo() {
      await this.player.playVideo();
      // Do something after the playVideo command
    },
    playing() {
      console.log('\o/ we are watching!!!')
    }
  },
  beforeDestroy: function() {
    console.log('wating_view beforeDestroy');
    this.$EventBus.$off(EventBus.MOVIE_VIEW);
  },
  computed: {
    player() {
      return this.$refs.youtube.player
    }
  }
}
</script>

<style scoped>
.movieView {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 16;
}

.back {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.center_box {
  position: absolute;
  top: 380px;
  left: 50%;
  transform: translate(-50%);
}
</style>



