// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';

import PerfectScrollbar from 'vue2-perfect-scrollbar'
import 'vue2-perfect-scrollbar/dist/vue2-perfect-scrollbar.css'
import VueYoutube from 'vue-youtube'

Vue.use(VueYoutube)
import VueSweetalert2 from 'vue-sweetalert2';
const options = {
  confirmButtonColor: '#41b882',
  cancelButtonColor: '#ff7674'
}
import ProgressBar from 'vuejs-progress-bar'
Vue.use(ProgressBar)
Vue.use(VueSweetalert2, options);
Vue.use(PerfectScrollbar, {
  name: 'scroll'
});

import router from './router';
import moment from 'moment';
import VueMomentJS from 'vue-momentjs';
import io from 'socket.io-client';

import BootstrapVue from 'bootstrap-vue';
import StrCheck from './javascript_library/StrCheck';

import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue/dist/bootstrap-vue.css'

import axios from 'axios';

import SocketManager from './socket/SocketManager.js'
import EventBus from './lib/EventBus.js'
// Vue.prototype.$c_socket = new SocketManager(io, 'http://localhost:5001/Campaign', this);
// Vue.prototype.$e_socket = new SocketManager(io, 'http://localhost:5001/EventPage', this);


Vue.prototype.$http = axios;
Vue.prototype.$http.options.emulateJSON = true;
Vue.prototype.$str_check = new StrCheck();

Vue.use(VueMomentJS, moment);
Vue.use(BootstrapVue);
// Vue.use(router);
Vue.prototype.$room_id = 0;
Vue.config.productionTip = false;
Vue.prototype.$campaign_io = null;
Vue.prototype.$event_io = null;

let nowStage = '';

//io start///////////////////////////////////////////////
function connectIO() {
  Vue.prototype.$io = io('http://localhost:5000', {
    transports: ['websocket']
  });
}

connectIO();

Vue.prototype.$now_time = 0;
Vue.prototype.$EventBus = new Vue();

Vue.prototype.$io.on('connect', () => {
  console.log('connection');
  let d = {};
  d.msg = 'client hi';
  Vue.prototype.$io.emit('msg', d);
  // you can also do this.messages.push(data)
});
Vue.prototype.$io.on('msg', (data) => {
  console.log('data.msg:', data.msg);
  // this.time_io.disconnect();
  // you can also do this.messages.push(data)
});
Vue.prototype.$io.on('time', (data) => {
  data = parseInt(data);
  Vue.prototype.$now_time = data;
  // this.time_io.disconnect();
  // you can also do this.messages.push(data)
  if (nowStage == EventBus.MAIN) {
    Vue.prototype.$EventBus.$emit(EventBus.MAIN_EVENT, data);
  } else if (nowStage == EventBus.CAMPAIGN) {
    Vue.prototype.$EventBus.$emit(EventBus.CAMPAIGN_EVENT, data);
  } else if (nowStage == EventBus.EVENT_PAGE) {
    Vue.prototype.$EventBus.$emit(EventBus.EVENT_PAGE_EVENT, data);
  }
});
Vue.prototype.$io.on('error', (error) => {
  console.log('error');
  alert('Time server error로 접속이 안됩니다.');
  // you can also do this.messages.push(data)
});
Vue.prototype.$io.on('disconnect', () => {
  console.log('time disconnect');
  alert('Time server 접속이 끊어졌습니다. 3초 후에 다시 연결 합니다.');
  setTimeout(function() {
    Vue.prototype.$io = null;
    connectIO();
  }, 3000);
  // you can also do this.messages.push(data)
});


router.beforeEach((to, from, next) => {
  // ...
  console.log('change', to);
  // console.log('change', from);
  nowStage = to.name;
  next();
});
//io end//////////////////////////////////////////////
/* eslint-disable no-new */

new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
});



