<template>
<!-- <div class="main" ref='main' v-on:click='CampaignMove'> -->
<div class="main" ref='main'>
  <div class="header">
    <img class="top_image" :src="'/static/logo_big.png'">
    <div class="top_btn_box">
      <div class="btn_unit">이벤트기록</div>
      <div class="btn_unit">게임 설명</div>
    </div>
  </div>
  <div class="top">
    <img class="top_bg" :src="'/static/top_bg.png'" />
    <img class="top_icon" :src="'/static/caution_icon.png'" />
    <span class="top_ex">아래 이벤트를 클릭하면 자세한 내용을 보실 수 있습니다</span>
  </div>
  <scroll class="list_body" v-bind:style="{ height: height + 'px' }" @ps-scroll-y="scrollHanle">

    <!-- <div class="list_box" v-for="(value, index) in list" :style="{backgroundImage: 'url(' + listImage(index, 4, 'listbg_') + ')'}"> -->
    <div v-if="showList(value.time)" class="list_box" v-on:click="CampaignMove(value.id)" v-for="(value, index) in systems"
      :style="{backgroundImage: 'linear-gradient(135deg, ' + color_array[getColorIndex(index)][0] + ', ' + color_array[getColorIndex(index)][1] + ')'}">
      <div class="front_box">
        <img class="icon" v-bind:src="imageDir(value.image_name)" width="165" height="165" />
      </div>
      <div class="list_ex">
        <div class="list_ex_unit" id="list_title" ref="list_title">
          {{ value.events[0].event_name }}
        </div>
        <div class="list_ex_unit">
          <span class="list_icon">
            <img :src="'/static/trophy_white_icon.png'" width="25" height="24" />
            <span class="minu_text">{{ value.events[0].winner }}</span>
          </span>
          <span class="list_icon">
            <img :src="'/static/bong_white_icon.png'" width="25" height="24" />
            <span class="minu_text">{{ value.events[0].bong }}배</span>
          </span>
        </div>
        <div class="list_ex_unit" id="list_time">
          {{ timeUtill.getChangeTime($moment, value.time, now_time) }}
        </div>
      </div>
    </div>
  </scroll>
</div>
</template>

<script>
import NetValues from '../NetValues';
import EventBus from '../lib/EventBus.js'
import TimeUtill from '../lib/TimeUtill.js'

export default {
  name: 'Main',
  data: () => {
    return {
      timeUtill: new TimeUtill(),
      height: 0,
      now_time: 0,
      systems: [],
      color_array: [
        ['#46c999', '#46b5c9'],
        ['#ff903f', '#ff593f'],
        ['#64a4eb', '#4677c9'],
        ['#bd72dd', '#8548be']
      ]
    }
  },
  mounted: function() {
    console.log('mounted');
    this.getList();
    this.$EventBus.$on(EventBus.MAIN_EVENT, (data) => {
      this.now_time = data;
    });
    this.now_time = this.$now_time;
    // this.$refs.main.addEventListener('resize', 1257);
    // this.$refs.main.addEventListener('resize', this.getWindowWidth);
    this.getWindowWidth();
    window.addEventListener('message', this.receiveMessage, false);

  },
  methods: {
    CampaignMove: function(id) {
      this.$router.push({
        name: 'Campaign',
        params: {
          campaign_id: id
        }
      });
    },
    receiveMessage(event) {
      if(typeof(event.data) == Number){
        this.$router.push({
          name: 'Campaign',
          params: {
            campaign_id: event.data
          }
        });
      }
      console.log('receiveMessage:', event.data);
    },
    scrollHanle(e) {
      if (Math.round(e.target.scrollHeight - e.target.scrollTop) == e.target.clientHeight) {
        this.getList(this.systems.length + 10);
      }
    },
    imageDir(name) {
      if (name != '') {
        return "https://s3.ap-northeast-2.amazonaws.com/minuapp.com.rsp/campaign_images/" + name;
      } else {
        return '';
      }
    },
    getColorIndex(i) {
      return i % this.color_array.length;
    },
    getList(end = 5) {
      let data = {};
      let start = 0;
      data.start = start;
      data.end = end;
      this.$http.post('http://localhost:3001/api/getList', data)
        .then((response) => {
          console.log('qery:', response.data);
          if (response.data.res == NetValues.REQ_OK) {
            // this.$router.push('/Admin');REQ_DB_ERRO
            this.setList(response.data.qery);
          } else {
            // this.msgRouter(response.data.res);
          }
        })
        .catch(function(error) {
          console.log('error', error);
        });
    },
    setList(data) {
      this.systems = [];
      for (let i = 0; i < data.length; ++i) {
        this.systems[i] = data[i];
      }
    },
    showList(t) {
      let s_time = parseInt(t);
      let v = (s_time > this.now_time);
      if (!v) {
        this.getList(this.systems.length);
      }
      return v;
    },
    getWindowWidth: function(event) {
      console.log('resize:', this.$refs.main.clientHeight);
      // this.height = this.$refs.main.clientHeight - 34 - 280;
      this.height = 1257 - 34 - 280;
    },
    resize: function() {
      console.log('ssssssssss');
    }
    // listImage(i = 0, n = 3, src = "dummy_logo_") {
    //   i = i % n;
    //   let str = "/static/" + src + i + ".png";
    //   console.log('str:', str);
    //   return str;
    // }
  },
  beforeDestroy: function() {
    this.$EventBus.$off(EventBus.MAIN_EVENT);
    console.log('main beforeDestroy');
  },
  computed: {}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main {
  position: relative;
  margin: 0px;
  /* max-width: 720px; */
  width: 720px;
  /* max-height: 1246px; */
  height: inherit;
  text-align: center;
  background-color: #f7f7f7;
  /* outline: 2px dashed blue; */
  display: block;
}

.header {
  position: relative;
  height: 180px;
  background-color: #ffffff;
  /* outline: 2px dashed blue; */
}

.top_image {
  position: relative;
  top: 20px;
  /* outline: 2px dashed blue; */
}

.top_icon {
  position: relative;
  top: 38px;
}

.top {
  position: relative;
  height: 100px;
  max-width: 100%;
  /* outline: 2px dashed blue; */
  /* background-image: url(/static/top_bg.png); */
}

.top_bg {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100px;
  max-width: 100%;
  /* outline: 2px dashed blue; */
  /* max-width: 720px; */
}

.top_ex {
  position: relative;
  top: 40px;
  max-width: 100%;
  color: #d2d2d2;
  font-size: 17px;
}

.list_body {
  position: relative;
  top: 0px;
  max-width: 720px;
  /* /* outline: 2px dashed blue; */
  */
  /* max-height: 932vh; */
  height: inherit;
  /* min-height: 1066px; */
  /* overflow-x: hidden;
  overflow-y: auto; */
  text-align: center;
  background-color: #ffffff;
}

.list_ul {
  position: relative;
  top: 0px;
  width: 100%;
}

.bottom_side {
  position: relative;
  top: 0px;
  width: 100%;
  /* outline: 2px dashed blue; */
  /* max-height: 932vh; */
  height: inherit;
  /* min-height: 1066px; */
  overflow-x: hidden;
  overflow-y: auto;
  text-align: center;
}

.top_btn_box {
  position: fixed;
  right: 30px;
  top: 45px;
}

.btn_unit {
  position: relative;
  margin: 5px;
  cursor: pointer;
  width: 111px;
  height: 38px;
  background-image: url(/static/_btn.png);
  padding-top: 7px;
  font-size: 15px;
  color: #a7a7a7;
}

.list_box {
  position: relative;
  /* width: 613px; */
  max-width: 100%;
  height: 230px;
  /* left: 50%;
  margin-left: -306px; */
  margin-left: 50px;
  margin-right: 50px;
  margin-top: 28px;
  /* outline: 2px dashed blue; */
  border-radius: 20px;
  /* background-color: #46c1ac; */
  /* background-image: linear-gradient(135deg, #46c999, #46b5c9); */
  /* background-image: url(/static/listbg_0.png); */
}

.front_box {
  position: absolute;
  border-radius: 17px;
  background-color: #fff;
  width: 195px;
  height: 195px;
  left: 17px;
  top: 16px;
}

.icon {
  position: relative;
  top: 15px;

}

.list_ex {
  position: relative;
  top: 22px;
  padding-left: 255px;
  text-align: left;
  max-width: inherit;
  /* outline: 2px dashed blue; */
}

.list_ex_unit {
  position: relative;
  margin-bottom: 15px;
  max-width: 100%;
  /* outline: 2px dashed blue; */
}

#list_title {
  position: relative;
  font-size: 30px;
  color: #ffffff;
}

.list_icon {
  position: relative;
  width: 125px;
  height: 40px;
  display: table-cell;
  /* outline: 2px dashed blue; */
}

.minu_text {
  position: relative;
  vertical-align: middle;
  color: #000000;
  font-size: 20px;
  margin-left: 5px;
  /* line-height: 1;
  outline: 2px dashed blue; */
}

#list_time {
  position: relative;
  font-family: 'CoreGTD4', Helvetica, Arial, sans-serif;
  max-width: 310px;
  height: auto;
  margin-left: -10px;
  margin-top: -7px;
  border-radius: 20px;
  border: 2px solid #fff;
  background-color: rgba(255, 255, 255, 0.1);
  font-size: 41px;
  color: #fff;
  padding-left: 10px;
  padding-right: 10px;
  text-align: center;

}
</style>



