'use strict'
const StrCheck = function() {
  //숫자만 가능
  this.number_check = function(v) {
    var re = new RegExp("[^0-9]+", "gi");
    if (re.test(v)){
      return false;
    }
    return true;
  }

  this.number_change = function(str) {
    var re = new RegExp("[^0-9]+", "gi");
    return str.replace(re, "");
  }

  this.en_check = function(oj, array1, array2) {
    var re = new RegExp("[^a-z_]+", "g");
    var str_var = "";
    for (var i = 0; i < array1.length; i++) {
      str_var = oj.form[array1[i]].value;
      if (re.test(str_var)) {
        oj.form[array1[i]].value = "";
        alert(array2[i]);
        oj.form[array1[i]].focus();
        return false;
      } else if (str_var == "") {
        oj.form[array1[i]].value = "";
        alert("게시판 이름을 적어 주세요.");
        oj.form[array1[i]].focus();
        return false;
      } else if (str_var.length < 3) {
        oj.form[array1[i]].value = "";
        alert("게시판 이름은 3자 이상입니다.");
        oj.form[array1[i]].focus();
        return false;
      }
    }
    return true;
  }

  this.email_check = function(str) {
    var re = new RegExp("^[-/\_a-zA-Z0-9.]+(@[-/\_a-zA-Z0-9]+)((.[a-z]{2,3})(.[a-z]{2,3})?)$", "gi");
    if (re.test(str)) {
      return true;
    }
    return false;
  }

  this.void_check = function(str) {
    var regExp = /\s/g;
    var result = str.replace(regExp, '');
    if (result != "") {
      return false;
    } else {
      return true;
    }
  }

  this.void_return = function(str) {
    var regExp = /\s/g;
    var result = str.replace(regExp, '');
    return result;
  }

  this.isNumberKey = function(_value) {
    var _pattern1 = /[^0-9\.]/;
    if (_pattern1.test(_value)) {
      return false;
    }
    var _pattern2 = /[\.]{2}$/;
    if (_pattern2.test(_value)) {
      return false;
    }
    // 소수점 둘째자리까지만 입력가능
    var _pattern2 = /^\d*[.]\d{3}$/;
    if (_pattern2.test(_value)) {
      return false;
    }
    // return true;
    return true;

  }
    // this.isNumberKey = function(_value) {
    // //   var _pattern1 = /[^0-9\.]/;
    // //   if (_pattern1.test(_value)) {
    // //     return false;
    // //   }
    //   // var _pattern2 = /[\.]{2}/;
    //   // if (_pattern2.test(_value)) {
    //   //   return false;
    //   // }
    //   // 소수점 둘째자리까지만 입력가능
    //   var _pattern2 = /(\d)*[\.]{0, 1}*(\d){0, 2}*/;
    //   if (_pattern2.test(_value)) {
    //     return true;
    //   }
    //   // return true;
    //   return false;
    //
    // }
}
module.exports = StrCheck;



