<template>
<div class="campaign">
  <div class="header">
    <img class="top_image" :src="'/static/Campagin/logo_small.png'">
    <div class="topBtn" v-on:click='back()'>
      <img :src="'/static/Campagin/prev_btn.png'">
    </div>
  </div>
  <div class="top_bg">
  </div>
  <div class="content">
    <TopImage></TopImage>
    <div class="title">
      <div class="title_front">
        <img class="title_con" :src="imageDir(data.image_name)" width="45" height="45">
        <span class="title_con">
          {{ events.event_name }}
        </span>
      </div>
      <div class="title_back">
        <div class="title_unit">
          <span class="max_count"><img :src="'/static/Campagin/trophy_black_icon.png'"></span>
          <span class="max_count">
            {{ events.winner }}
          </span>
        </div>
        <div class="title_unit">
          <span class="max_count"><img :src="'/static/Campagin/bong_black_icon.png'"></span>
          <span class="max_count">
            {{ events.bong }}배
          </span>
        </div>
      </div>
    </div>
    <div class="center_box">
      <div class="item_image">
        <img class="i_image" :src="imageDir(events.image_name, 'event_images')" width="550" height="550">
      </div>
    </div>
    <div class="bottom_time">
      <span class="time_title">
        남은시간
      </span>
      <span class="time_con">
        {{timeUtill.getChangeTime($moment, data.time, now_time, DATE_ID)}}<span class="time_con_end">{{timeUtill.getChangeTime($moment, data.time, now_time, TIME_ID)}}</span>
      </span>
    </div>
    <div class="bottom_btn_body">
      <span class="bottom_title">
        선착순
        <span class="bottom_title_end">
          {{connect_count}}/{{ data.participant }}
        </span>
      </span>
      <span class="bottom_con" v-on:click="eventMove()" v-show='start_view'>
        <img :src="'/static/Campagin/event_start_btn.png'">
      </span>
      <span class="bottom_con" v-on:click="copy()" v-show='copy_view'>
        <img :src="'/static/Campagin/event_share_btn.png'">
      </span>
    </div>
  </div>
  <inputWin></inputWin>
</div>
</template>

<script>
import TimeUtill from '../lib/TimeUtill.js';
import NetValues from '../NetValues';
import DateUtill from '../lib/DateUtill';
import MyDate from '../javascript_library/MyDate';
import MySystem from '../javascript_library/System';
import io from 'socket.io-client';
import SocketManager from '../socket/SocketManager.js';
import EventBus from '../lib/EventBus.js';
import InputWin from './InputWin';
import TopImage from './TopImage';

let _this = this;
export default {
  name: "Campaign",
  components: {
    InputWin,
    TopImage
  },
  data() {
    return {
      msg: "캠패인",
      start_view: false,
      copy_view: false,
      connect_count: 0,
      timeUtill: new TimeUtill(),
      mySystem: new MySystem(),
      c_socket: null,
      data: {
        time: 0,
        event_name: '',
        winner: 0,
        bong: 0,
        participant: 0,
        image_name: ''
      },
      events: {
        event_name: '',
        image_name: ''
      },
      all_event: [],
      height: 0,
      now_time: 0,
      DATE_ID: TimeUtill.DATE,
      TIME_ID: TimeUtill.TIME
    };
  },
  mounted() {
    this.now_time = this.$now_time;
    this.getCampaign(this.$route.params.campaign_id);
    this.$EventBus.$on(EventBus.MOVE_EVENT, (data) => {
      let result = this.timeUtill.moveBeforeCheck(this.data.time, this.now_time, this.connect_count, this.data.participant);
      if (result == TimeUtill.OK) {
        this.$router.push({
          name: 'EventPage',
          params: {
            campaign_id: this.$route.params.campaign_id,
            nicname: data.nicname,
            email: data.email,
            phone: data.phone
          }
        });
      } else {
        if (result == TimeUtill.TIME_OVER) {
          alert('접속 시간이 지났습니다.');
        } else if (result == TimeUtill.COUNT_OVER) {
          console.log('초과');
          // this.$swal('알림', '정원이 초과되어 더 이상 접속이 불가능합니다.', '확인');
          alert('정원이 초과되어 더 이상 접속이 불가능합니다.');
        }
          this.$router.push('/');
      }

    });
  },
  methods: {
    imageDir(name, root_name = 'campaign_images') {
      if (name != '') {
        return "https://s3.ap-northeast-2.amazonaws.com/minuapp.com.rsp/" + root_name + '/' + name;
      } else {
        return '';
      }
    },
    EventnMove: function(id) {
      let result = this.timeUtill.moveBeforeCheck(this.data.time, this.now_time, this.connect_count, this.data.participant);
      if (result == TimeUtill.OK) {
        this.$EventBus.$emit(EventBus.INPUT_WIN, true);
      } else {
        if (result == TimeUtill.TIME_OVER) {
          alert('접속 시간이 지났습니다.');
        } else if (result == TimeUtill.TIME_OVER) {
          alert('정원이 초과되어 더 이상 접속이 불가능합니다.');
        }
        this.$router.push('/');
      }
    },
    showList(t) {
      let s_time = parseInt(t);
      let v = (s_time > this.now_time);
      if (!v) {
        this.getList(this.systems.length);
      }
      return v;
    },
    back() {
      this.$router.push('/');
    },
    copy() {
      // let v = 'localhost:8080' + this.$route.path;
      console.log('this.$route:', window.location.host);
        let v =  window.location.origin + '/main.html?campaign=' + this.$route.params.campaign_id;
      this.mySystem.copyToClipboard(v);
      alert('링크주소가 복사되었습니다.');
    },
    eventMove() {
      // this.EventnMove(this.$route.params.campaign_id);
      this.$EventBus.$emit(EventBus.INPUT_WIN, true);
    },
    getCampaign(id) {
      let data = {};
      data.campaign_id = id;
      this.$http.post('http://localhost:3001/api/getCampaign', data)
        .then((response) => {
          console.log('qery:', response.data);
          if (response.data.res == NetValues.REQ_OK) {
            // this.$router.push('/Admin');REQ_DB_ERRO
            this.now_time = this.$now_time;
            this.data = response.data.qery[0];
            let event_data = this.getFristEvent(this.data);
            this.events = event_data[0];
            this.all_event  = event_data[1];
            this.$EventBus.$emit(EventBus.SET_TOP_IMAGE, this.events, this.all_event);
            if (!this.timeUtill.showList(this.data.time, this.now_time)) {
              this.$router.push('/');
              console.log('end');
            } else {
              this.$EventBus.$on(EventBus.CAMPAIGN_EVENT, (data) => {
              this.now_time = data;
                let view = this.timeUtill.countdown(this.$moment, this.data.time, this.now_time);
                this.start_view = !view;
                this.copy_view = view;
                // if (!this.timeUtill.showList(this.data.time, this.now_time)) {
                //   this.$router.push('/');
                // }
              });
              this.c_socket = new SocketManager(io, 'http://localhost:5001/Campaign', this, EventBus.CAMPIGN_ROOM, this.$EventBus);

              this.c_socket.start(this.$route.params.campaign_id);
              this.$EventBus.$on(EventBus.CAMPIGN_ROOM, (data) => {
                console.log('campign data.count:', data.count);
                this.connect_count = parseInt(data.count);
              });
            }
          } else {
            // this.msgRouter(response.data.res);
          }
        })
        .catch(function(error) {
          console.log('error', error);
        });
    },
    getFristEvent(v) {
      let event = null;
      let new_event = [];
      console.log('v.events.length:', v.events.length);
      for (let i = 0; i < v.events.length; ++i) {
        if (parseInt(v.events[i].root_view) == 1) {
          event = v.events[i];
          new_event = v.events.splice(i, 1);
          break;
        }
      }
      //test start///////////////////////////
      // for (let i = 0; i < 8; ++i) {
      //   v.events.push(v.events[0]);
      // }
      //test end///////////////////////////

      v.events.unshift(new_event[0]);
      return [event, v.events];
    }
  },
  beforeDestroy: function() {
    console.log('campaign beforeDestroy');
    this.$EventBus.$off(EventBus.MOVE_EVENT);
    this.$EventBus.$off(EventBus.CAMPAIGN_EVENT);
    this.$EventBus.$off(EventBus.CAMPIGN_ROOM);
    if (this.c_socket != null) {
      this.c_socket.end();
    }
  }
};
</script>

