<template>
<div class="startView" ref='startView' v-show="win_view">
  <div class="back"></div>
  <div class="center_box">
    <div class="bong_start">
      <img :src="imageLocalDir('bong_start.png')" width="259" height="329">
    </div>
  </div>
</div>
</template>

<script>
import EventBus from '../lib/EventBus.js';
export default {
  name: "StartView",
  data() {
    return {
      win_view: false
    }
  },
  mounted() {
    this.$EventBus.$on(EventBus.START_VIEW, (view) => {
      console.log('EventBus.START_VIEW data:', view);
        this.win_view = view;
    });
  },
  methods: {
    imageDir(name, root_name = 'campaign_images') {
      if (name != '') {
        return "https://s3.ap-northeast-2.amazonaws.com/minuapp.com.rsp/" + root_name + '/' + name;
      } else {
        return '';
      }
    },
    imageLocalDir(name, root_name = 'Campagin') {
      if (name != '') {
        return "/static/" + root_name + '/' + name;
      } else {
        return '';
      }
    },
  },
  beforeDestroy: function() {
    console.log('wating_view beforeDestroy');
    this.$EventBus.$off(EventBus.START_VIEW);
  }
}
</script>

<style scoped>
@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(-360deg);
  }
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(-360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}

.startView {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 18;
}

.back {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.center_box {
  position: absolute;
  top: 80px;
  left: 50%;
  transform: translate(-50%);
}

.bong_start {
  position: absolute;
  top: 400px;
  left: 50%;
  transform: translate(-50%);
  opacity: 0.7;
}

</style>



