<template>
<div class="winView" ref='winView' v-show="win_view">
  <div class="back"></div>
  <div class="center_box">
    <img class="back_bg" :src="imageLocalDir('Congratu_bg.png')" width="698" height="959">
    <div class="center_image">
      <img class="image" :src="imageDir(item_iamge, 'event_images')">
    </div>
    <div class="title">
      <div class="top_title">
        <span class="front_title">{{ nicname }}</span><span class="back_title">님</span>
      </div>
      <div class="bottom_title">
        우승하셨습니다
      </div>
    </div>
    <div class="ex">
      <span class="front_ex">{{ item_name }}</span><span class="back_ex"> 획득하셨습니다</span>
    </div>
  </div>
</div>
</template>

<script>
import EventBus from '../lib/EventBus.js';
export default {
  name: "WinView",
  data() {
    return {
      win_view: false,
      nicname: '',
      item_name: '',
      item_iamge: ''
    }
  },
  mounted() {
    this.$EventBus.$on(EventBus.WIN_VIEW, (data) => {
      console.log('EventBus.WIN_VIEW data:', data);
      if(data != null){
        this.win_view = data.view;
        this.nicname = data.nicname;
        this.item_name = data.event_name;
        this.item_iamge = data.image_name;
      } else {
        this.win_view = false;
      }
    });
  },
  methods: {
    imageDir(name, root_name = 'campaign_images') {
      if (name != '') {
        return "https://s3.ap-northeast-2.amazonaws.com/minuapp.com.rsp/" + root_name + '/' + name;
      } else {
        return '';
      }
    },
    imageLocalDir(name, root_name = 'Campagin') {
      if (name != '') {
        return "/static/" + root_name + '/' + name;
      } else {
        return '';
      }
    },
  },
  beforeDestroy: function() {
    console.log('wating_view beforeDestroy');
    this.$EventBus.$off(EventBus.WIN_VIEW);
  }
}
</script>

<style scoped>
@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(-360deg);
  }
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(-360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}

.winView {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 18;
}

.back {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.center_box {
  position: absolute;
  top: 80px;
  left: 50%;
  transform: translate(-50%);
}

.top_icon {
  position: relative;
}

.center_image {
  position: absolute;
  top: 284px;
  left: 50%;
  transform: translate(-50%);
  width: 698px;
  height: 698px;
}
.title{
  position: absolute;
  font-family: 'CoreGTD7', Helvetica, Arial, sans-serif;
  width: 100%;
  top: 230px;
  font-size: 40px;
}
.front_title{
  color: rgb(252, 88, 11);
}
.ex{
  position: absolute;
  font-family: 'CoreGTD5', Helvetica, Arial, sans-serif;
  width: 100%;
  font-size: 23px;
  bottom: 106px;
}
.front_ex{
  color: rgb(252, 68, 11);
}
</style>



