<template>
<div class="resultView" ref='rsultView' v-show="win_view">
  <div class="back"></div>
  <div class="center_box">
    <div class="bong_pass" v-if="pass">
      <img :src="imageLocalDir('bong_pass.png')" width="259" height="329">
    </div>
    <div class="bong_failed" v-if="failed">
      <img :src="imageLocalDir('bong_failed.png')" width="259" height="329">
    </div>
  </div>
</div>
</template>

<script>
import EventBus from '../lib/EventBus.js';
export default {
  name: "ResultView",
  data() {
    return {
      win_view: false,
      pass: false,
      failed: false,
    }
  },
  mounted() {
    this.$EventBus.$on(EventBus.RESULT_VIEW, (view, win=false) => {
      console.log('EventBus.RESULT_VIEW data:', view);
        this.win_view = view;
        if(view){
          if(win){
            this.pass = true;
            this.failed = false;
          } else {
            this.pass = false;
            this.failed = true;
          }
        }
    });
  },
  methods: {
    imageDir(name, root_name = 'campaign_images') {
      if (name != '') {
        return "https://s3.ap-northeast-2.amazonaws.com/minuapp.com.rsp/" + root_name + '/' + name;
      } else {
        return '';
      }
    },
    imageLocalDir(name, root_name = 'Campagin') {
      if (name != '') {
        return "/static/" + root_name + '/' + name;
      } else {
        return '';
      }
    },
  },
  beforeDestroy: function() {
    console.log('wating_view beforeDestroy');
    this.$EventBus.$off(EventBus.RESULT_VIEW);
  }
}
</script>

<style scoped>
@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(-360deg);
  }
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(-360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}

.resultView {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 18;
}

.back {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.center_box {
  position: absolute;
  top: 80px;
  left: 50%;
  transform: translate(-50%);
}

.bong_pass {
  position: absolute;
  top: 400px;
  left: 50%;
  transform: translate(-50%);
  opacity: 0.7;
}

.bong_failed {
  position: absolute;
  top: 400px;
  left: 50%;
  transform: translate(-50%);
  opacity: 0.7;
}
</style>



