<template>
<div class="event">
  <div class="header">
    <img class="top_image" :src="'/static/Campagin/logo_small.png'">
    <div class="topBtn" v-on:click='back()'>
      <img :src="'/static/Campagin/prev_btn.png'">
    </div>
  </div>
  <div class="top_bg">
  </div>
  <div class="content">
    <TopImage></TopImage>
    <div class="title">
      <div class="title_front">
        <img class="title_con" :src="imageDir(data.image_name)" width="45" height="45">
        <span class="title_con_text">
          {{ events.event_name }}
        </span>
      </div>
      <div class="title_back">
        <div class="title_unit">
          <span class="max_count"><img :src="'/static/Campagin/people_black_icon.png'"></span>
          <span class="max_count">
            {{connect_count}}/{{ data.participant }}
          </span>
        </div>
        <div class="title_unit">
          <span class="max_count"><img :src="'/static/Campagin/trophy_black_icon.png'"></span>
          <span class="max_count">
            {{ events.winner_done }}/{{ events.winner }}
          </span>
        </div>
        <div class="title_unit">
          <span class="max_count"><img :src="'/static/Campagin/bong_black_icon.png'"></span>
          <span class="max_count">
            {{ events.bong }}배
          </span>
        </div>
      </div>
      <div class="time_bar">
        <img :src="'/static/Campagin/time_icon.png'" />
        <progress-bar class="time_progress" :options="options" :value="progress_value" />
      </div>
    </div>
    <div class="center_box">
      <div class="item_image">
        <img class="i_image" :src="imageDir(events.image_name, 'event_images')" width="450" height="450">
        <div class="bottom_time" v-if='time_view'>
          {{timeUtill.getChangeTime($moment, data.time, now_time, TIME_ID)}}
        </div>
        <scroll class="scroll" ref='ps' @ps-scroll-y="scrollHanle" v-html='content' tagname="div" v-show="chat_toggor">

        </scroll>
        <div class="chat_btn" @click="chatToggor">
          <div class="chat_btn_img" v-bind:style="{ opacity: chat_opacity }">
          </div>
        </div>
      </div>
    </div>
    <div class="chatting_body">
      <input class="chatting_input" type="text" ref="chatting_input" v-on:keyup.enter="submit" v-model="chat_content" />
    </div>
    <div class="bottom_btn_body">
      <span class="b_bottom" @click="setSelectID(0)">
        <div class="btn_bar" v-bind:style="{ backgroundColor: bar_color[0], top: bar_height_value[0] }">

        </div>
        <div class="btn_back_image">
          <img :src="imageLocalDir(btn_back_image[0])" />
        </div>
        <div class="btn_front_image" id="btn_front_image_1" v-bind:style="{ opacity: btn_opacity[0] }">
          <img :src="'/static/Campagin/game_kawi_icon.png'" />
        </div>
        <div class="game_count">
          {{ changeNo(0) }}
        </div>
      </span>
      <span class="b_bottom" @click="setSelectID(1)">
        <div class="btn_bar" v-bind:style="{ backgroundColor: bar_color[1], top: bar_height_value[1]  }">

        </div>
        <div class="btn_back_image">
          <img :src="imageLocalDir(btn_back_image[1])" />
        </div>
        <div class="btn_front_image" id="btn_front_image_2" v-bind:style="{ opacity: btn_opacity[1] }">
          <img :src="'/static/Campagin/game_bawi_icon.png'" />
        </div>
        <div class="game_count">
          {{ changeNo(1) }}
        </div>
      </span>
      <span class="b_bottom" @click="setSelectID(2)">
        <div class="btn_bar" v-bind:style="{ backgroundColor: bar_color[2], top: bar_height_value[2] }">

        </div>
        <div class="btn_back_image">
          <img :src="imageLocalDir(btn_back_image[2])" />
        </div>
        <div class="btn_front_image" id="btn_front_image_3" v-bind:style="{ opacity: btn_opacity[2] }">
          <img :src="'/static/Campagin/game_bo_icon.png'" />
        </div>
        <div class="game_count">
          {{ changeNo(2) }}
        </div>
      </span>
      <span class="b_bottom" @click="setSelectID(3)">
        <div class="btn_bar" v-bind:style="{ backgroundColor: bar_color[3], top: bar_height_value[3] }">

        </div>
        <div class="btn_back_image">
          <img :src="imageLocalDir(btn_back_image[3])" />
        </div>
        <div class="btn_front_image" id="btn_front_image_4" v-bind:style="{ opacity: btn_opacity[3] }">
          <img :src="'/static/Campagin/game_bong_icon.png'" />
        </div>
        <div class="game_count">
          {{ changeNo(3) }}
        </div>
      </span>
    </div>
  </div>
  <StartView></StartView>
  <MovieView></MovieView>
  <WatingView></WatingView>
  <ResultView></ResultView>
  <WinView></WinView>
</div>
</template>

<script>
import TimeUtill from '../lib/TimeUtill.js';
import NetValues from '../NetValues'
import DateUtill from '../lib/DateUtill'
import MyDate from '../javascript_library/MyDate'
import io from 'socket.io-client';
import SocketManager from '../socket/SocketManager.js';
import EventBus from '../lib/EventBus.js';
import PerfectScrollbar from 'vue2-perfect-scrollbar';
import Math2 from '../javascript_library/Math2';
import TopImage from './TopImage';
import WatingView from './WatingView';
import MovieView from './MovieView';
import WinView from './WinView';
import ResultView from './ResultView';
import StartView from './StartView';

let _this = this;
export default {
  name: "Event",
  components: {
    TopImage,
    WatingView,
    MovieView,
    WinView,
    ResultView,
    StartView
  },
  data() {
    return {
      msg: "이벤트",
      start_game: false,
      now_time: 0,
      time_view: false,
      enabled: false,
      connect_count: 0,
      select_id: -1,
      chat_toggor: true,
      chat_opacity: 0.3,
      BAR_MAX: 110,
      BAR_MIN: 64,
      bar_height: [0, 0, 0, 0],
      bar_height_value: ['-64px', '-64px', '-64px', '-64px'],
      timeUtill: new TimeUtill(),
      myMath: new Math2(),
      content: '',
      btn_opacity: [0.3, 0.3, 0.3, 0.3],
      bar_color: ['#e4e4e4', '#e4e4e4', '#e4e4e4', '#e4e4e4'],
      btn_back_image: ['game_btn_1.png', 'game_btn_1.png', 'game_btn_1.png', 'game_btn_1.png'],
      gameCount: [0, 0, 0, 0],
      data: {
        time: 0,
        event_name: '',
        image_name: '',
        bong: 0,
        participant: 0,
        event_count: 0
      },
      events: {
        event_name: '',
        image_name: '',
        winner: 0,
        winner_done: 0
      },
      all_event: [],
      e_socket: null,
      chat_content: '',
      DATE_ID: TimeUtill.DATE,
      TIME_ID: TimeUtill.TIME,
      progress_max: 100,
      progress_value: 100,
      timer_id: -1,
      options: {
        text: {
          color: '#FFFFFF',
          shadowEnable: true,
          shadowColor: '#000000',
          fontSize: 0,
          fontFamily: 'Helvetica',
          dynamicPosition: false,
          hideText: true
        },
        progress: {
          color: '#ff5f32',
          backgroundColor: '#dedede'
        },
        layout: {
          height: 56,
          width: 56,
          verticalTextAlign: 41,
          horizontalTextAlign: 1,
          zeroOffset: 0,
          strokeWidth: 10,
          progressPadding: 0,
          type: 'circle'
        }
      }
    }
  },
  mounted() {
    this.now_time = this.$now_time;
    console.log('this.now_time:', this.now_time);
    if (this.$route.params.email == undefined) {
      // alert('정상적인 접속이 아닙니다.');
      this.$router.push('/');
      // this.getCampaign(this.$route.params.campaign_id);
    } else {
      this.getCampaign(this.$route.params.campaign_id);
    }
    this.setEnabled(false);
  },
  methods: {
    imageDir(name, root_name = 'campaign_images') {
      if (name != '') {
        return "https://s3.ap-northeast-2.amazonaws.com/minuapp.com.rsp/" + root_name + '/' + name;
      } else {
        return '';
      }
    },
    setBarHeight(index, min, max) {
      let p = this.BAR_MAX - this.BAR_MIN;
      this.bar_height[index] = (this.BAR_MIN + (min / max * p)) * -1;
      console.log('(min/max*p):' + index + ':' + (min / max * p));
      console.log('this.bar_height[index]:' + index + ':' + this.bar_height[index]);
      this.bar_height_value[index] = this.bar_height[index] + 'px';
    },
    setSelectID(id) {
      if (this.enabled) {
        this.select_id = id;
        this.btn_opacity = [1, 1, 1, 1];
        this.bar_color = ['#e4e4e4', '#e4e4e4', '#e4e4e4', '#e4e4e4'];
        this.btn_back_image = ['game_btn_1.png', 'game_btn_1.png', 'game_btn_1.png', 'game_btn_1.png'];
        if (id == -1) {
          this.btn_opacity = [0.3, 0.3, 0.3, 0.3];
        } else if (id == -2) {
          this.btn_opacity = [1, 1, 1, 1];
        } else {
          this.bar_color[this.select_id] = '#ffcb9e';
          this.btn_back_image[this.select_id] = 'game_btn_2.png';
          this.e_socket.game_data.select_id = this.select_id;
          if (this.getLife()) {
            this.e_socket.send(SocketManager.GAME_DATA, this.e_socket.game_data);
          }
        }
      }
    },
    getLife() {
      if (this.e_socket != null) {
        return this.e_socket.game_data.life;
      } else {
        return true;
      }
    },
    setEnabled(v) {
      this.enabled = v;
      this.bar_color = ['#e4e4e4', '#e4e4e4', '#e4e4e4', '#e4e4e4'];
      this.btn_back_image = ['game_btn_1.png', 'game_btn_1.png', 'game_btn_1.png', 'game_btn_1.png'];
      if (v) {
        this.btn_opacity = [1, 1, 1, 1];
      } else {
        if (this.e_socket != null) {
          this.e_socket.game_data.select_id = -1;
        }
        this.gameCount = [0, 0, 0, 0];
        this.setBarHeight(0, 0, 1);
        this.setBarHeight(1, 0, 1);
        this.setBarHeight(2, 0, 1);
        this.setBarHeight(3, 0, 1);
        this.btn_opacity = [0.3, 0.3, 0.3, 0.3];
      }
    },
    imageLocalDir(name, root_name = 'Campagin') {
      if (name != '') {
        return "/static/" + root_name + '/' + name;
      } else {
        return '';
      }
    },
    chatToggor() {
      this.chat_toggor = !this.chat_toggor;
      if (this.chat_toggor) {
        this.chat_opacity = 0.3;
        let scroll = this.$refs.ps.$el;
        scroll.scrollTop = scroll.scrollHeight;
        this.$refs.chatting_input.focus();
      } else {
        this.chat_opacity = 1;
      }
    },
    scrollHanle(e) {
      // e.target.scrollTop = e.target.scrollHeight;
      // if (Math.round(e.target.scrollHeight - e.target.scrollTop) == e.target.clientHeight) {
      //   this.getList(this.systems.length + 10);
      // }
    },
    MainnMove: function() {
      this.$router.push('/');
    },
    changeNo: function(id) {
      return this.myMath.won(this.gameCount[id]);
    },
    submit(e) {
      if (this.$str_check.void_check(this.chat_content)) {
        this.chat_content = '';
        alert('내용을 입력해 주세요.');
        this.$refs.chatting_input.focus();
        return;
      }
      if (this.chat_content.length > 30) {
        this.chat_content = '';
        alert('30자이내만 입력 가능합니다.');
        this.$refs.chatting_input.focus();
        return;
      }
      this.chat_content = this.chat_content.trim();
      this.e_socket.send(SocketManager.CHAT, this.chat_content);
      this.setChat('나', this.chat_content);
      this.chat_content = '';
      this.$refs.chatting_input.focus();
    },
    getCampaign(id) {
      let data = {};
      data.campaign_id = id;
      this.$http.post('http://localhost:3001/api/getCampaign', data)
        .then((response) => {
          console.log('qery:', response.data);
          if (response.data.res == NetValues.REQ_OK) {
            // this.$router.push('/Admin');REQ_DB_ERRO
            this.now_time = this.$now_time;
            this.data = response.data.qery[0];
            let event_data = this.getFristEvent(this.data);
            this.events = event_data[0];
            this.all_event = event_data[1];
            this.$EventBus.$emit(EventBus.SET_TOP_IMAGE, this.events, this.all_event);
            if (!this.timeUtill.showList(this.data.time, this.now_time)) {
              // if (false) {
              alert('접속 시간이 지났습니다.');
              this.$router.push('/');
            } else {
              this.$EventBus.$on(EventBus.EVENT_PAGE_EVENT, (data) => {
                if (!this.start_game) {
                  this.now_time = data;
                  if (!this.timeUtill.showList(this.data.time, this.now_time)) {

                    if (this.time_view) {
                      this.time_view = false;
                      this.$EventBus.$emit(EventBus.WATIMG_VIEW, true);
                    }

                  } else {
                    if (!this.time_view) {
                      this.$EventBus.$emit(EventBus.WATIMG_VIEW, false);
                      this.time_view = true;
                    }
                  }
                }
              });
              let view = this.timeUtill.countdown(this.$moment, this.data.time, this.now_time);
              if (!view) {
                // if (true) {
                this.e_socket = new SocketManager(io, 'http://localhost:5001/EventPage', this, EventBus.EVENTPAGE_ROOM, this.$EventBus,
                  this.data.participant, this.$route.params.nicname, this.$route.params.email, this.$route.params.phone);
                this.e_socket.start(this.$route.params.campaign_id);
                this.$EventBus.$on(EventBus.EVENTPAGE_ROOM, (data) => {
                  console.log('eventpage data.count:', data.count);
                  this.connect_count = parseInt(data.count);
                  if (data.msg == 'count_over') {
                    alert('정원이 초과되어 더 이상 접속이 불가능합니다.');
                    this.$router.push('/');
                  } else if (data.msg == 'ok') {
                    this.$refs.chatting_input.focus();
                    // alert(data.msg);
                  } else if (data.msg == 'in') {
                    // alert(data.msg);
                    this.setChat(data.nicname, '님이 입장하셨습니다.', true);

                    // this.$refs.chat_view.innerHTML = this.content;
                    console.log('this.content:', this.content);
                  } else if (data.msg == 'repeated') {
                    if (data.check_id == 1) {
                      alert('중복된 닉네임입니다. 다시 접속해 주세요.');
                    } else if (data.check_id == 2) {
                      alert('중복된 이메일입니다. 다시 접속해 주세요.');
                    } else if (data.check_id == 3) {
                      alert('중복된 전화번호입니다. 다시 접속해 주세요.');
                    }
                    this.$router.go(-1);
                  } else if (data.msg == 'by') {
                    this.setChat(data.nicname, '님이 퇴장하셨습니다.', true);
                  }
                });
                this.$EventBus.$on(EventBus.EVENT_DATA, (data) => {
                  if (data.msg == EventBus.START_DELAY) {
                    this.data.event_count = data.event_count;
                    if (data.restart) {

                    }
                    this.setEnabled(false);
                    this.$EventBus.$emit(EventBus.START_VIEW, true);
                    this.$EventBus.$emit(EventBus.WIN_VIEW, null);
                    this.$EventBus.$emit(EventBus.RESULT_VIEW, false);
                    this.$EventBus.$emit(EventBus.WATIMG_VIEW, false);
                    this.$EventBus.$emit(EventBus.WIN_VIEW, null);
                    this.progress_max = parseInt(data.count_time);
                    this.progress_value = this.progress_max / this.progress_max * 100;
                    this.$EventBus.$emit(EventBus.SET_TOP_IMAGE_INDEX, parseInt(this.data.event_count));

                  } else if (data.msg == EventBus.EVENT_START) {

                    this.$EventBus.$emit(EventBus.START_VIEW, false);
                    if (this.getLife()) {
                      this.setEnabled(true);
                    }
                  } else if (data.msg == EventBus.YOUTUBE_START) {
                    let d = {};
                    d.view = true;
                    d.videoId = data.youtube_url;
                    this.$EventBus.$emit(EventBus.MOVIE_VIEW, d);
                    this.$EventBus.$emit(EventBus.WATIMG_VIEW, false);
                  } else if (data.msg == EventBus.YOUTUBE_STOP) {
                    let d = {};
                    d.view = false;
                    d.videoId = '';
                    this.$EventBus.$emit(EventBus.MOVIE_VIEW, d);
                  } else if (data.msg == EventBus.COUNTDOWN) {
                    let count_t = parseInt(data.count_time);
                    this.progress_value = (count_t / this.progress_max) * 100;
                  } else if (data.msg == EventBus.EVENT_STOP) {
                    this.progress_value = 0;
                    this.$EventBus.$emit(EventBus.WATIMG_VIEW, true);
                  } else if (data.msg == EventBus.EVENT_COMPUTER_DONE) {
                    this.e_socket.send(SocketManager.GET_DATA, null);

                  } else if(data.msg == EventBus.GAME_DONE){
                    this.MainnMove();
                  }
                });
                this.$EventBus.$on(SocketManager.CHAT, (data) => {
                  this.setChat(data.nicname, data.chat);
                });
                this.$EventBus.$on(SocketManager.GET_DATA, (data) => {
                  console.log('SocketManager.GET_DATA data:', data);
                  if (this.getLife()) {
                    this.$EventBus.$emit(EventBus.WATIMG_VIEW, false);
                    if (!data.u_info.life) {
                      this.$EventBus.$emit(EventBus.RESULT_VIEW, true, true);
                      let _this = this;
                      this.timer_id = setTimeout(function() {
                        let d = {};
                        d.view = true;
                        d.nicname = data.u_info.nicname;
                        d.event_name = _this.events.event_name;
                        d.image_name = _this.events.image_name;
                        _this.$EventBus.$emit(EventBus.WIN_VIEW, d);
                        _this.$EventBus.$emit(EventBus.RESULT_VIEW, false);
                      }, 2000);
                    } else {
                      this.$EventBus.$emit(EventBus.RESULT_VIEW, true, false);
                    }
                  }
                  this.events.winner_done = data.winner_done;
                });
                this.$EventBus.$on(SocketManager.GAME_DATA, (data) => {
                  console.log('gameCount:', data);
                  this.gameCount = data;
                  this.setBarHeight(0, this.gameCount[0], this.connect_count);
                  this.setBarHeight(1, this.gameCount[1], this.connect_count);
                  this.setBarHeight(2, this.gameCount[2], this.connect_count);
                  this.setBarHeight(3, this.gameCount[3], this.connect_count);
                });


              } else {
                console.log('this.now_time:', this.now_time);
                if (this.now_time == 0 || this.$route.params.email == undefined) {
                  alert('정상적인 접속이 아닙니다.');
                } else {
                  alert('입장 가능한 시간이 아닙니다.게임 시작 10분전부터 입장 가능합니다.');
                }
                this.$router.push('/');
              }
            }
          } else {
            // this.msgRouter(response.data.res);
          }
        })
        .catch(function(error) {
          console.log('error', error);
        });
    },
    setChat(nicname, chat, frist = false) {
      if (frist) {
        this.content += "<div class='chat_unit'><span class='chat_head2'>" + nicname + "</span><span class='chat_msg'>" + chat + "</span></div>";
      } else {
        this.content += "<div class='chat_unit'><span class='chat_head'>" + nicname + "</span><span class='chat_msg'>" + chat + "</span></div>";
      }

      let scroll = this.$refs.ps.$el;
      setTimeout(function() {
        // alert('a');
        scroll.scrollTop = scroll.scrollHeight;

      }, 30);
    },
    getFristEvent(v) {
      let event = null;
      let new_event = [];
      console.log('v.events.length:', v.events.length);
      for (let i = 0; i < v.events.length; ++i) {
        if (parseInt(v.events[i].root_view) == 1) {
          event = v.events[i];
          new_event = v.events.splice(i, 1);
          break;
        }
      }
      //test start///////////////////////////
      // for (let i = 0; i < 8; ++i) {
      //   v.events.push(v.events[0]);
      // }
      //test end///////////////////////////

      v.events.unshift(new_event[0]);
      return [event, v.events];
    }
  },
  beforeDestroy: function() {
    console.log('eventPage beforeDestroy');
    this.$EventBus.$off(EventBus.EVENT_PAGE_EVENT);
    this.$EventBus.$off(EventBus.EVENTPAGE_ROOM);
    this.$EventBus.$off(EventBus.EVENT_DATA);
    this.$EventBus.$off(SocketManager.CHAT);
    this.$EventBus.$off(SocketManager.GAME_DATA);
    this.$EventBus.$off(SocketManager.GET_DATA);
    clearTimeout(this.timer_id);
    if (this.e_socket != null) {
      this.e_socket.end();
    }
  },
  computed: {

  }
};
</script>

