<template>
<div class="topImage" ref='topImage'>
  <div class="contentBody" ref='ps'>
    <!-- <scroll class="contentBody" ref='ps' tagname="div" @ps-scroll-x="scrollHanle" axis="x"> -->
    <!-- <scroll class="contentBody" ref='contentBody' v-html='content' @ps-scroll-x="scrollHanle"> -->
    <div class="unit_body" ref="unit_body" v-bind:style="{ width: unit_width + 'px', left: left_x + 'px' }">
      <div class="image_unit" @click="viewImage()" v-for="(value, index) in systems">
        <div class="circle_mask">
          <!-- <img :src="imageLocalDir(value.image_name)" width="90" height="90" /> -->
            <img :src="imageDir(value.image_name, 'event_images')" width="90" height="90" />
        </div>
        <div class="circle_line">
        </div>
      </div>
      <div class="top_arrow" v-bind:style="{ left: top_icon_x + 'px' }">
        <img :src="imageLocalDir('choice_arrow_icon.png')" width="27" height="18" />
      </div>
    </div>
  </div>
  <div class="left_arrow" v-if='left_view' @click="moveList(false)" >
    <img :src="imageLocalDir('gift_left_arrow_btn.png')" width="25" height="54" />
  </div>
  <div class="rigth_arrow" v-if='right_view' @click="moveList(true)" >
    <img :src="imageLocalDir('gift_arrow_btn.png')" width="25" height="54" />
  </div>
</div>
</template>

<script>
import TimeUtill from '../lib/TimeUtill.js';
import EventBus from '../lib/EventBus.js';
export default {
  name: "TopImage",
  data() {
    return {
      timeUtill: new TimeUtill(),
      event_index: 0,
      systems: [],
      events: {
        event_name: '',
        image_name: ''
      },
      all_event: [],
      content: '',
      unit_width: 0,
      left_x: 0,
      left_view: false,
      right_view: false,
      TOP_MAX: 110,
      TOP_ICON_X: 42,
      top_icon_x: 42
    }
  },
  mounted() {
    this.$EventBus.$on(EventBus.SET_TOP_IMAGE, (data, all_event) => {
      this.events = data;
      this.all_event = all_event;
      this.systems= all_event;
      this.unit_width = (this.systems.length * this.TOP_MAX);
      console.log('this.unit_width:' + all_event.length);
      // this.moveBtn();
      this.setScrollPoint(this.event_index);
      this.moveBtn();
      // let _this = this;
      // function test(){
      //   setTimeout(function(){
      //     _this.event_index++;
      //     _this.setScrollPoint(_this.event_index);
      //     test();
      //   }, 1000);
      // }
      // test();
    });
    console.log('this.$refs.unit_body.clientX:' + this.$refs.unit_body.getBoundingClientRect().left);
    this.$EventBus.$on(EventBus.SET_TOP_IMAGE_INDEX, (data) => {
      this.event_index = data;
      this.setScrollPoint(this.event_index);
      this.moveBtn();
    });
  },
  methods: {
    imageDir(name, root_name = 'campaign_images') {
      if (name != '') {
        return "https://s3.ap-northeast-2.amazonaws.com/minuapp.com.rsp/" + root_name + '/' + name;
      } else {
        return '';
      }
    },
    imageLocalDir(name, root_name = 'Campagin') {
      if (name != '') {
        return "/static/" + root_name + '/' + name;
      } else {
        return '';
      }
    },
    setScrollPoint(index){
      if(this.systems.length > index){
        if(5 <= index){
          this.moveAutoList();
        }
        this.top_icon_x = this.TOP_ICON_X + (this.TOP_MAX * index);
      }
    },
    moveBtn(){
      if(this.systems.length > 5){
        if(this.left_x < 0){
          this.left_view = true;
        } else {
          this.left_view = false;
        }
        if((this.unit_width + this.left_x)/this.TOP_MAX <= 5){
          this.right_view = false;
        } else {
          this.right_view = true;
        }
      } else {
        this.right_view = false;
        this.left_view = false;
      }
    },
    moveList(right){
      if(right){
        if((this.unit_width + this.left_x)/this.TOP_MAX > 5){
          this.left_x -= this.TOP_MAX;
        }
      } else {
        if(this.left_x < 0){
          this.left_x += this.TOP_MAX;
        }
      }
      this.moveBtn();
    },
    moveAutoList(){
      let w = -1*((this.event_index-4)*this.TOP_MAX);
      this.left_x = w;
      this.moveBtn();
    },
    // setScrollPoint(v) {
    //   // const container = document.querySelector('. contentBody');
    //   // let scroll = this.$refs.ps.$el;
    //   // console.log('this.$refs.contentBody.$el.scrollWidth:', scroll.scrollHeight);
    // },
    viewImage() {

    },
    scrollHanle(e) {
      // e.target.scrollTop = e.target.scrollHeight;
      // if (Math.round(e.target.scrollHeight - e.target.scrollTop) == e.target.clientHeight) {
      //   this.getList(this.systems.length + 10);
      // }
    }
  },
  beforeDestroy: function() {
    console.log('eventPage beforeDestroy');
    this.$EventBus.$off(EventBus.SET_TOP_IMAGE);
    this.$EventBus.$off(EventBus.SET_TOP_IMAGE_INDEX);
  }
}
</script>

<style scoped>
.topImage {
  position: relative;
  width: 100%;
  height: 120px;
  border-radius: 10px;
  border: 2px solid #e5e5e5;
  background-color: #f3f3f3;
  text-align: left;
  /* outline: 2px dashed blue; */
}

.top_arrow {
  position: absolute;
  left: 42px;
  top: -10px;
  width: 27px;
  height: 18px;
}

.left_arrow {
  position: absolute;
  left: 10px;
  top: 30px;
  width: 25px;
  height: 54px;
  cursor: pointer;
}

.rigth_arrow {
  position: absolute;
  right: 10px;
  top: 30px;
  width: 25px;
  height: 54px;
  cursor: pointer;
}

.contentBody {
  position: relative;
  top: 2px;
  width: 550px;
  height: 110px;
  left: 50%;
  transform: translate(-50%);
  overflow: hidden;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
  /* outline: 2px dashed blue; */
}

.image_unit {
  position: relative;
  text-align: center;
  display: inline-flex;
  vertical-align: middle;
  width: 90px;
  height: 90px;
  margin-left: 10px;
  margin-right: 10px;
  /* outline: 2px dashed blue; */
  /* outline: 2px dashed blue; */
  /* opacity: 0.3; */
  /* background-color: rgb(189, 127, 32); */
}

.unit_body {
  position: absolute;
  text-align: left;
  width: 700px;
  height: 90px;
  top: 10px;
  left: 0px;
}

.circle_mask {
  position: absolute;
  width: 90px;
  height: 90px;
  clip-path: circle(50%);
  shape-outside: circle(50%);
  background-color: rgb(255, 255, 255);
}

.circle_line {
  position: absolute;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  border: 2px solid #dadada;
}
</style>



