<template>
<div class="inputWin" ref='inputWin' v-show="win_view">
  <div class="win">
    <div class="input_box">
      <div class="input_unit">
        <input type="text" v-model="form.nicname" name="nicname" ref='nicname' id="nicname" class="input_user" placeholder="닉네임 입력" />
      </div>
      <div class="input_unit">
        <input type="text" v-model="form.email" name="email" ref='email' id="email" class="input_user" placeholder="이메일 입력" />
      </div>
      <div class="input_unit">
        <input type="text" v-model="form.phone" name="phone" ref='phone' id="phone" class="input_user" placeholder="숫자만 입력" />
      </div>
    </div>
    <div class="bottom">
      <div class="check" ref="check" @click="clickWin">
        <img class="click_img" v-show='click_view' src="/static/Campagin/check_icon.png" />
      </div>
      <span class="info_btn" ref="info_btn">
        <img src="/static/Campagin/info_btn.png" />
      </span>
    </div>
    <div class="send_btn" ref="send_btn" @click="send">
      <img class="click_img" src="/static/Campagin/info_ok_btn_2.png" />
    </div>
  </div>
</div>
</template>

<script>
import TimeUtill from '../lib/TimeUtill.js';
import EventBus from '../lib/EventBus.js';
export default {
  name: "ImputWin",
  data() {
    return {
      win_view: false,
      click_view: false,
      timeUtill: new TimeUtill(),
      form: {
        nicname: '',
        email: '',
        phone: ''
      }
    }
  },
  mounted() {
    this.$EventBus.$on(EventBus.INPUT_WIN, (data) => {
      this.win_view = data;
    });
  },
  methods: {
    send() {
      if (!this.timeUtill.showList(this.$parent.data.time, this.$parent.now_time)) {
        alert('접속 시간이 지났습니다.');
        this.$router.push('/');
      } else {

        if (this.$str_check.void_check(this.form.nicname)) {
          this.form.nicname = '';
          this.$refs.nicname.focus();
          alert('닉네임을 입력해 주세요.');
          return;
        } else if (this.form.nicname.length < 3 || this.form.nicname.length >= 10) {
          this.form.nicname = '';
          this.$refs.nicname.focus();
          alert('닉네임은 3자리 이상 10자리 이하로 입력해 주세요.');
          return;
        }
        this.form.nicname = this.form.nicname.trim();


        if (this.$str_check.void_check(this.form.email)) {
          this.form.email = '';
          this.$refs.email.focus();
          alert('이메일을 입력해 주세요.');
          return;
        } else if (!this.$str_check.email_check(this.form.email.trim())) {
          this.form.email = '';
          this.$refs.email.focus();
          alert('이메일이 잘못되었습니다. 정확한 이메일을 입력해 주세요.');
          return;
        }
        this.form.email = this.form.email.trim();


        if (this.$str_check.void_check(this.form.phone)) {
          this.form.phone = '';
          this.$refs.phone.focus();
          alert('전화번호를 입력해 주세요.');
          return;
        } else if (!this.$str_check.number_check(this.form.phone.trim())) {
          this.form.phone = '';
          this.$refs.phone.focus();
          alert('전화번호는 숫자만 입력해 주세요.');
          return;
        }
        this.form.phone = this.form.phone.trim();

        if (!this.click_view) {
          alert('개인정보사용동의를 하셔야 게임 진행이 가능합니다.');
          return;
        }
        let d = {};
        d.nicname = this.form.nicname;
        d.email = this.form.email;
        d.phone = this.form.phone;
        this.$EventBus.$emit(EventBus.MOVE_EVENT, d);

      }
    },
    clickWin() {
      this.click_view = !this.click_view;
    }
  },
  beforeDestroy: function() {
    console.log('eventPage beforeDestroy');
    this.$EventBus.$off(EventBus.INPUT_WIN);
  }
}
</script>

<style scoped>
.inputWin {
  position: absolute;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 5;
  background-color: rgba(6, 6, 6, 0.5);
}

.win {
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -58%);
  width: 633px;
  height: 540px;
  background-image: url(/static/Campagin/info_popup_bg.png);
}

input {
  background-color: transparent;
  color: #444444;
  border-style: hidden;
  /* border-style: solid; */
  font-size: 24px;
  /* border-width: 1px; */
}

.input_box {
  position: relative;
  top: 140px;
  left: 57px;
}

.input_unit {
  position: relative;
  margin-bottom: 42px;
}

.bottom {
  position: relative;
  top: 150px;
  left: 90px;
  /* outline: 2px dashed blue; */
  text-align: left;
  width: 455px;
  height: 16px;
}

.check {
  position: relative;
  width: 21px;
  height: 16px;
  cursor: pointer;
  /* outline: 2px dashed blue; */
  background-color: rgba(170, 24, 163, 0);
}

.click_img {
  position: relative;
  top: -5px;
  left: -2px;
}

.info_btn {
  position: absolute;
  right: 0px;
  cursor: pointer;
  top: -3px;
}

.send_btn {
  position: relative;
  top: 180px;
  cursor: pointer;
}
</style>



