import EventBus from '../lib/EventBus.js'
import GameData from './GameData.js'

export default class SocketManager {
  constructor(io, url, vue_root, room_event, eventBus, countMax = -1, nicname = '', email = '', phone = '') {
    this.io = io;
    this.url = url;
    this.socket = null;
    this.move = null;
    this.room_event = room_event;
    this.eventBus = eventBus;
    this.countMax = countMax;
    // this.nicname = nicname;
    // this.email = email;
    // this.phone = phone;
    this.game_data = new GameData(nicname, email, phone);
  }
  start(room = -1) {
    console.log('start socket');
    if (this.socket == null) {
      this.socket = this.io(this.url, {
        transports: ['websocket']
      });
      this.socket.on('connect', () => {
        console.log('connection campaign');
        let d = {};
        d.msg = 'client hi campaign';
        d.countMax = this.countMax;
        d.room = room;
        if (this.email != '') {
          // this.game_data.nicname = this.nicname;
          // this.game_data.email = this.email;
          // this.game_data.phone = this.phone;
          d.u_info = this.game_data;
        }
        // this.socket.emit('msg', d);
        if (room != -1) {
          this.socket.emit('room', d);
        }
        // you can also do this.messages.push(data)
      });
      this.socket.on('room', (data) => {
        this.eventBus.$emit(this.room_event, data);
        // you can also do this.messages.push(data)
      });
      this.socket.on(SocketManager.CHAT, (data) => {
        console.log('data.msg:', data.msg);
        this.eventBus.$emit(SocketManager.CHAT, data);
        // this.time_io.disconnect();
        // you can also do this.messages.push(data)
      });
      this.socket.on('msg', (data) => {
        console.log('data.msg:', data.msg);
        // this.time_io.disconnect();
        // you can also do this.messages.push(data)
      });
      this.socket.on(EventBus.EVENT_DATA, (data) => {
        console.log('data:', data);
        this.eventBus.$emit(EventBus.EVENT_DATA, data);
        // this.time_io.disconnect();
        // you can also do this.messages.push(data)
      });
      this.socket.on(SocketManager.GAME_DATA, (data) => {
        console.log('data:', data);
        this.eventBus.$emit(SocketManager.GAME_DATA, data);
        // this.time_io.disconnect();
        // you can also do this.messages.push(data)
      });
      this.socket.on(SocketManager.GET_DATA, (data) => {
        console.log('data:', data);
        this.eventBus.$emit(SocketManager.GET_DATA, data);
        this.game_data = data.u_info;
        // this.time_io.disconnect();
        // you can also do this.messages.push(data)
      });
      this.socket.on('error', (error) => {
        console.log('error');
        alert('Time server error로 접속이 안됩니다.');
        // you can also do this.messages.push(data)
      });
      this.socket.on('disconnect', () => {
        console.log('disconnect');
        // alert('Server 접속이 끊어졌습니다. 메인 페이지로 이동합니다.');
        if (this.move != null) {
          vue_root.$router.push(this.move);
        }
      });
    }
  }
  send(msg_name, msg) {
    this.socket.emit(msg_name, msg);
  }
  end(move = null) {
    this.move = move;
    if (this.socket != null) {
      this.socket.disconnect();
      this.socket = null;
    }
  }
  static get CHAT() {
    return 'chat';
  }
  static get GAME_DATA() {
    return 'game_data';
  }
  static get GET_DATA() {
    return 'get_data';
  }
}



