export default class TimeUtill {
  constructor() {
    console.log('new TimeUtill');
  }

  getChangeTime(moment, count_time, now_time, all_index = 0, date_mark = 'D-') {

    now_time = Math.floor(now_time / 1000) * 1000;
    if (count_time == 0 || now_time == 0) {
      return '';
    } else {
      let s_time = parseInt(count_time);
      let time = s_time - now_time;
      let countdown = moment.duration(time - 1000, 'milliseconds');
      if(countdown.valueOf() <= 0){
        countdown = moment.duration(0);
      }
      // this.countdown(moment, count_time, now_time);
      if (all_index == TimeUtill.ALL) {
        return date_mark + this.getDiffDays(moment, count_time, now_time) + " " +
          this.changeOneToTwo(moment.duration(countdown).hours()) + ":" +
          this.changeOneToTwo(moment.duration(countdown).minutes()) + ":" +
          this.changeOneToTwo(moment.duration(countdown).seconds());
      } else if (all_index == TimeUtill.DATE) {
        return date_mark + this.getDiffDays(moment, count_time, now_time);
      } else if (all_index == TimeUtill.TIME) {
        return this.changeOneToTwo(moment.duration(countdown).hours()) + ":" +
          this.changeOneToTwo(moment.duration(countdown).minutes()) + ":" +
          this.changeOneToTwo(moment.duration(countdown).seconds());
      } else {
        return '';
      }

    }
  }
  getDiffDays(moment, count_time, now_time){
    let end = moment(count_time);
    let now =  moment(now_time);
    let diff = end.diff(now, 'd');
    return diff;
  }
  changeOneToTwo(t) {
    t = parseInt(t);
    if (t < 10) {
      t = '0' + t;
    } else {
      t += '';
    }

    return t;
  }
  showList(count_time, now_time) {
    let s_time = parseInt(count_time);
    now_time = parseInt(now_time);
    let v = (s_time >= now_time);
    return v;
  }
  countdown(moment, count_time, now_time, n=10) {
    now_time = Math.floor(now_time / 1000) * 1000;
    let s_time = parseInt(count_time);
    let time = s_time - now_time;
    let countdown = moment.duration(time - 1000, 'milliseconds').valueOf();
    if(countdown > 60*n*1000){
      return true;
    } else {
      return false;
    }
  }
  moveBeforeCheck(count_time, now_time, connect_count, participant) {
    if (!this.showList(count_time, now_time)) {
        return TimeUtill.TIME_OVER;
    } else {
      if (connect_count < participant) {
        return TimeUtill.OK;
      } else {
        return TimeUtill.COUNT_OVER;
      }
    }
  }
  static get ALL() {
    return 0;
  }
  static get DATE() {
    return 1;
  }
  static get TIME() {
    return 2;
  }


  static get OK() {
    return 0;
  }
  static get TIME_OVER() {
    return 1;
  }
  static get COUNT_OVER() {
    return 2;
  }
}



