'use strict'
const DateUtill = function() {
  this.mysqlTimeStampToDate = function(timestamp) {
    timestamp = timestamp.replace("T", " ");
    timestamp = timestamp.replace(".000Z", "");
    let t = timestamp.split(/[- :]/);
    return new Date(t[0], t[1] - 1, t[2], t[3] || 0, t[4] || 0, t[5] || 0);
  }
  this.converterToDate = function(y, m, d, h, mi) {
    if (parseInt(m) < 10) {
      m = '0' + m;
    }
    if (parseInt(d) < 10) {
      d = '0' + d;
    }
    if (parseInt(h) < 10) {
      h = '0' + h;
    }
    if (parseInt(mi) < 10) {
      mi = '0' + mi;
    }
    return y + '-' + m + '-' + d + ' ' + h + ':' + mi;
  }
}
module.exports = DateUtill;



