<template>
<div class="watingView" ref='watingView' v-show="win_view">
  <div class="back"></div>
  <div class="center_box">
    <img class="top_icon" :src="imageLocalDir('loading_bong_icon.png')" width="127" height="170">
    <div class="icon">
      <img class="bottom_icon" :src="imageLocalDir('loading_icon.png')" width="126" height="126">
    </div>
  </div>
</div>
</template>

<script>
import EventBus from '../lib/EventBus.js';
export default {
  name: "WatingView",
  data() {
    return {
      win_view: false
    }
  },
  mounted() {
    this.$EventBus.$on(EventBus.WATIMG_VIEW, (data) => {
      this.win_view = data;
    });
  },
  methods: {
    imageDir(name, root_name = 'campaign_images') {
      if (name != '') {
        return "https://s3.ap-northeast-2.amazonaws.com/minuapp.com.rsp/" + root_name + '/' + name;
      } else {
        return '';
      }
    },
    imageLocalDir(name, root_name = 'Campagin') {
      if (name != '') {
        return "/static/" + root_name + '/' + name;
      } else {
        return '';
      }
    },
  },
  beforeDestroy: function() {
    console.log('wating_view beforeDestroy');
    this.$EventBus.$off(EventBus.WATIMG_VIEW);
  }
}
</script>

<style scoped>
@-moz-keyframes spin { 100% { -moz-transform: rotate(-360deg); } }
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(-360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(-360deg); transform:rotate(-360deg); } }

.watingView {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 18;
}
.back{
  position: relative;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.center_box{
  position: absolute;
  top: 380px;
  left: 50%;
  transform: translate(-50%);
}
.icon{
  position: absolute;
  top: 180px;
  left: 0px;
  /* transform: translate(-50%); */
  -webkit-animation:spin 1.5s linear infinite;
    -moz-animation:spin 1.5s linear infinite;
    animation:spin 1.5s linear infinite;
}
</style>



